export type Edition = 2022 | 2023;

// Convert this string array to a type, so that we can use it below to define other types.
// Based on https://codyarose.com/blog/object-keys-from-array-in-typescript/
export const events = [
  "conferenceRegistration",
  "hackathonRegistration",
  "speedDating",
  "workshops",
  "conference",
  "hackathon",
  "presentations",
] as const;

export type EventIntervals = {
  conferenceRegistration: [string, string];
  hackathonRegistration: [string, string];
  speedDating: string;
  workshops: string;
  conference: string;
  hackathon: string;
  presentations: string;
};

export type EventDates = {
  [K in (typeof events)[number]]: string;
};

/**
 * This type stores edition-specific information.
 */
export type EditionData = {
  edition: Edition;
  eventIntervals: EventIntervals;
};
