import { Link, useTranslation } from "gatsby-plugin-react-i18next";

import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";

import { FaPeopleCarry } from "react-icons/fa";
import {
  GiCoffeeCup,
  GiPlasticDuck,
  GiTeamIdea,
  GiTrophyCup,
} from "react-icons/gi";
import { GoTools } from "react-icons/go";
import { RiFilePaper2Fill } from "react-icons/ri";

import { EditionData, events } from "../../data";
import useEventDates from "../../hooks/useEventDates";

import * as styles from "./schedule.module.css";
import React from "react";

type ScheduleProps = {
  editionData: EditionData;
};

const Schedule: React.FC<ScheduleProps> = ({ editionData }) => {
  const edition = editionData.edition;
  const dates = useEventDates(editionData);

  const { t } = useTranslation();

  const contentStyle = {
    background: "var(--schedule-color-1)",
    color: "var(--light2)",
    boxShadow: "0 3px 0 var(--accent)",
  };
  const iconStyle = { borderRight: "7px solid var(--schedule-color-1)" };
  const icons = [
    <GiCoffeeCup />,
    <GiPlasticDuck />,
    <FaPeopleCarry />,
    <GoTools />,
    <RiFilePaper2Fill />,
    <GiTeamIdea />,
    <GiTrophyCup />,
  ];
  const links = [
    "/conference",
    "/",
    "",
    "/workshops",
    "/conference",
    "/#Hackathon",
    "",
  ];

  return (
    <div className={`page ${styles.schedule}`} id={"schedule"}>
      <h1 className={`title ${styles.scheduleTitle}`}>{t("title")}</h1>
      <h2 className={`date ${styles.scheduleSubtitle}`}>{edition}</h2>

      <VerticalTimeline className={styles.verticalTimeline}>
        {events
          .map((eventName) => dates[eventName])
          .map((date, index) => (
            <VerticalTimelineElement
              key={index}
              contentStyle={contentStyle}
              contentArrowStyle={iconStyle}
              dateClassName={styles.verticalTimelineElementDate}
              iconClassName={styles.scheduleIcon}
              date={date}
              icon={icons[index]}
            >
              {(links[index] && edition == 2023) || [3, 4].includes(index) ? (
                <Link
                  to={(edition < 2023 ? "/" + edition : "") + links[index]}
                  className={styles.link}
                >
                  <h3 className={styles.verticalTimelineElementTitle}>
                    {t(`events.${events[index]}`)}
                  </h3>
                </Link>
              ) : (
                <h3 className={styles.verticalTimelineElementTitle}>
                  {t(`events.${events[index]}`)}
                </h3>
              )}
            </VerticalTimelineElement>
          ))}
      </VerticalTimeline>
    </div>
  );
};

export default Schedule;
